    // import axios from 'axios'
    import imgurApi from '@/api/imgur';
    import zh_TW from 'vee-validate/dist/locale/zh_TW';
    
    export default {
        name: 'Imgur',
        data() {
            return {
                dict: {
                    custom: {
                        "imgFile.fs.image": {
                            required: ''
                        },
                    }
                },
                imgFile: {
                    file: null, //  input type="file" 
                    fs: {
                        name: '', // input
                        thumbnail: null, // input
                        size: null, // input
                        image: null,
                    },
                    title: '', // 
                    des: '', // 
                },
                uped: {
                    link: null,
                    id: null,
                },
            }
        },
        methods: {
            // 
            showFile(e) {
                let vm = this,
                    reader = new FileReader();

                vm.imgFile.file = e.target.files[0]; // input type="file" 
                vm.imgFile.fs.name = vm.imgFile.file.name; // input
                vm.imgFile.fs.size = Math.floor(vm.imgFile.file .size * 0.001) + 'KB'; // input
                vm.imgFile.fs.thumbnail = window.URL.createObjectURL(vm.imgFile.file); // input
                vm.imgFile.title = vm.imgFile.fs.name; //  input 

                reader.addEventListener('load',(e)=>{
                    cl(e)
                    vm.imgFile.fs.image = e.target.result; // inputdata
                });
                reader.readAsDataURL(vm.imgFile.file);
            },
            // 
            resetFile() {
                let vm = this
                vm.imgFile.file = null
                vm.imgFile.title = null
                vm.imgFile.des = null
                vm.imgFile.fs.image = null
                vm.imgFile.fs.name = null
                vm.imgFile.fs.size = null
                vm.imgFile.fs.thumbnail = null
            },
            // Imgur Api event
            // 
            getPic(id) {
                imgurApi.getImgur({id}, (res)=>{
                    cl('getImg', res)
                })
            },
            // 
            delPic(id) {
                imgurApi.delImgur({id}, (res)=>{
                    cl(id, 'getImg del OK')
                })
            },
            // 
            submit() {
                this.$validator.validateAll().then((result) => {
                    if (result) {
                        let image = this.imgFile.file,
                            title = this.imgFile.title,
                            description = this.imgFile.des
                        imgurApi.upload({image, title, description}, (res)=>{
                            alert('')
                            this.uped.link = res.data.link;
                            this.uped.id = res.data.id;
                            this.resetFile()
                        })
                    }
                })
            },
            // submit_noUse() {
            //     let vm = this,
            //         settings = {
            //             async: false,
            //             crossDomain: true,
            //             processData: false,
            //             contentType: false,
