let log = process.env.VUE_APP_LOG;
/* eslint-disable */
global.cl = (...arg) => {       // 
    if (log === 'true') {
        console.log(...arg)
    }
}

global.clTb = (...arg) => {     // 
    if (log === 'true') {
        console.table(...arg)
    }
}

global.clDir = (...arg) => {    // 
    if (log === 'true') {
        console.dir(...arg)
    }
}