<template>
    <div class="store_box upFileBox">
        <input v-bind="$attrs"
               :type="type"
               accept=".jpg,.png,.gif"
               :id="upId"
               class="upFile"
               @change="upFileChange" />
        <label :for="upId" class="upFileView text-center posr">
            {{ title }}
            <template v-if="value">
                <img :src="value" class="posa" />
            </template>
        </label>
        <span v-if="error" class="text-danger">{{ error }}</span>
    </div>
</template>

<script>
    import imgurApi from '@/api/imgur';

    export default {
        name: 'UpFileBox',
        $_veeValidate: {
            value() {
                return this.innerValue;
            }
        },
        props: {
            value: {
                type: String,
            },
            upId: {
                type: String,
                required: true,
            },
            title: {
                type: String,
                default() {
                    return ''
                },
            },
            type: {
                type: String,
                default: 'text',
                validator: val => {
                    return (
                        ['url', 'text', 'password', 'email', 'search', 'file'].indexOf(val) !== -1
                    );
                }
            },
            error: {
                type: String,
                required: false
            },
        },
        data() {
            return {
                innerValue: 'initial',  // vee-validate  input type File  W3C  value  data 
            }
        },
        methods: {
            upFileChange(e) {
                let files = e.target.files || e.dataTransfer.files,
                    reader = new FileReader(),
                    image = e.target.files[0];
                if (!files.length) return false
                // imgur api 
                imgurApi.upload({image}, (res)=>{
                    alert('')
                    this.$emit('input', res.data.link);
                })
                //  api 
                // reader.addEventListener('load',(e)=>{
                //     cl(e)
                //     this.$emit('input', e.target.result);   // inputdata
                // });
                // reader.readAsDataURL(files[0]);
            },
            removeImage: function () {
                this.$emit('input', '');
            }
        },
    }
</script>
