import jsonBinApi from '@/api/jsonbin.io';
import changeMarvel from '@/mixins/changeMarvel'
import {
    mapMutations,
} from 'vuex';

export default {
    data() {
        return {
            fakeCouple: [],
        }
    },
    mixins: [
        changeMarvel,
    ],
    methods: {
        //  Vuex
        ...mapMutations('marvel', [
            'setMarvel',
            'setMarvelLen',
        ]),
        // 
        byJsonBinGet() {
            jsonBinApi.byJsonBinGetMarvelCouple((res)=>{
                this.setMarvel(res);
                this.setMarvelLen(res.length);
                this.fakeCouple = [...res];
                this.changeMarvelApiSingle(res);
            })
        },
    },
    created() {
        this.byJsonBinGet();
    },
}